function convertToNumbers(obj) {
  const numericFields = ["purchase_cost", "mileage_with_load", "mileage_without_load", "maintenance", "capacity", "annual_distance", "life", "diesel_price", "loading_unloading", "toll", "route_expenses", "driver_expenses", "tyres"];
  const converted = {
    ...obj
  };
  numericFields.forEach(field => {
    if (converted[field]) {
      converted[field] = parseFloat(converted[field]);
    }
  });
  return converted;
}
